/**
 * Orchestrates the step update by handling:
 * - progress bar width
 * - navigation buttons (prev, next, finish)
 * - progress bar corner styling
 *
 * @param {number} currentStep - The current (1-based) step
 * @param {number} totalSteps - Total number of steps
 * @param {HTMLElement} progressBar - The .progress-bar element
 * @param {HTMLElement} currentStepEl - The element displaying the current step number
 * @param {HTMLElement} prevButton - The "Previous" button
 * @param {HTMLElement} nextButton - The "Next/Finish" button
 */
export function updateStep(
    currentStep,
    totalSteps,
    progressBar,
    currentStepEl,
    prevButton,
    nextButton
) {
    updateProgressBar(currentStep, totalSteps, progressBar, currentStepEl);
    updateNavigationButtons(currentStep, totalSteps, prevButton, nextButton);
    updateProgressBarCorner(currentStep, totalSteps, progressBar);
}

/**
 * Updates the progress bar based on the current step.
 *
 * @param {number} currentStep - The current (1-based) step
 * @param {number} totalSteps - Total number of steps
 * @param {HTMLElement} progressBar - The .progress-bar element
 * @param {HTMLElement} currentStepEl - The element displaying the current step number
 */
export function updateProgressBar(
    currentStep,
    totalSteps,
    progressBar,
    currentStepEl
) {
    currentStepEl.textContent = currentStep.toString();
    const percentage = (currentStep / totalSteps) * 100;
    progressBar.style.width = `${percentage}%`;
    progressBar.setAttribute('aria-valuenow', percentage.toString());
}

/**
 * Adds a rounded corner to the right side of the progress bar on the last step.
 * Removes it otherwise.
 *
 * @param {number} currentStep - The current (1-based) step
 * @param {number} totalSteps - Total number of steps
 * @param {HTMLElement} progressBar - The .progress-bar element
 */
export function updateProgressBarCorner(currentStep, totalSteps, progressBar) {
    if (currentStep === totalSteps) {
        // Delay so that the progress bar transitions fully before rounding
        setTimeout(() => {
            progressBar.classList.add('rounded-end-0');
        }, 600);
    } else {
        progressBar.classList.remove('rounded-end-0');
    }
}

/**
 * Updates the navigation buttons (Prev/Next/Finish) based on the current step.
 *
 * @param {number} currentStep - The current (1-based) step
 * @param {number} totalSteps - Total number of steps
 * @param {HTMLElement} prevButton - The "Previous" button
 * @param {HTMLElement} nextButton - The "Next/Finish" button
 */
export function updateNavigationButtons(
    currentStep,
    totalSteps,
    prevButton,
    nextButton
) {
    // Enable/disable the "Previous" button
    prevButton.disabled = currentStep === 1;

    // --- PREV BUTTON ---
    if (currentStep === 1) {
        prevButton.classList.add('fade');
        prevButton.classList.remove('show');
    } else {
        prevButton.classList.add('show');
        setTimeout(() => {
            prevButton.classList.remove('fade');
            prevButton.classList.remove('show');
        }, 75);
    }

    // --- NEXT BUTTON ---
    if (currentStep === totalSteps) {
        // On the last step, switch to "Finish" mode
        nextButton.innerHTML = 'Fertig';
        nextButton.classList.add('btn-primary-dark');

        const summaryEl = document.getElementById('productConfiguratorSummary');
        if (summaryEl && summaryEl.classList.contains('show')) {
            // If the summary is already open
            nextButton.classList.add('show');
        }

        // Give Bootstrap a moment to set up its attributes
        setTimeout(() => {
            nextButton.setAttribute('data-bs-toggle', 'collapse');
            nextButton.setAttribute('data-bs-target', '#productConfiguratorSummary');
            nextButton.setAttribute('aria-expanded', 'false');
            nextButton.setAttribute('aria-controls', 'productConfiguratorSummary');
        }, 1);

        if (summaryEl) {
            // When the summary is shown:
            summaryEl.addEventListener('show.bs.collapse', () => {
                // Remove the toggle attributes, so the summary cannot be re-collapsed
                nextButton.classList.add('show');
                nextButton.removeAttribute('data-bs-toggle');
                nextButton.removeAttribute('data-bs-target');
                nextButton.removeAttribute('aria-expanded');
                nextButton.removeAttribute('aria-controls');
            });

            // Optionally prevent the summary from closing again
            summaryEl.addEventListener('hide.bs.collapse', (e) => {
                e.preventDefault(); // Prevents the summary from collapsing
            });
        }
    } else {
        // Standard "Weiter"
        nextButton.classList.remove('btn-primary-dark');
        nextButton.classList.remove('show');
        nextButton.innerHTML = 'Weiter';

        // Remove special toggles and reset to default carousel target
        setTimeout(() => {
            nextButton.removeAttribute('data-bs-toggle');
            nextButton.removeAttribute('aria-expanded');
            nextButton.removeAttribute('aria-controls');
            nextButton.setAttribute('data-bs-target', '#productConfiguratorCarousel');
        }, 300);
    }
}

/**
 * Sets the .carousel-inner height based on a given slide.
 * If no slide is provided, it uses the currently active slide.
 *
 * @param {HTMLElement} carouselInner - The .carousel-inner element
 * @param {HTMLElement} [slide] - Optional slide element; if not provided, uses the active one
 */
export function setCarouselHeight(carouselInner, slide) {
    let targetSlide = slide;
    if (!targetSlide) {
        targetSlide = carouselInner.querySelector('.carousel-item.active');
    }
    if (targetSlide) {
        carouselInner.style.height = `${targetSlide.offsetHeight}px`;
    }
}

/**
 * Removes the inline height, so the carousel becomes flexible again.
 *
 * @param {HTMLElement} carouselInner - The .carousel-inner element
 */
export function resetCarouselHeight(carouselInner) {
    carouselInner.style.height = '';
}

/**
 * Ensures the progress container is scrolled into view if it's not fully visible.
 *
 * @param {HTMLElement} configurator - The outer wrapper/container
 * @param {HTMLElement} configuratorProgress - The element containing the progress
 */
export function scrollToCarouselTop(configurator, configuratorProgress) {
    const rect = configuratorProgress.getBoundingClientRect();
    const isFullyVisible =
        rect.top >= 0 &&
        rect.left >= 0 &&
        rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
        rect.right <= (window.innerWidth || document.documentElement.clientWidth);

    if (!isFullyVisible) {
        configurator.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
}

/**
 * Removes the "overflow" class from the .carousel-inner element.
 *
 * This should be called before the slide transition starts.
 *
 * @param {HTMLElement} carouselInner - The .carousel-inner element
 */
export function removeCarouselOverflow(carouselInner) {
    if (carouselInner) {
        carouselInner.classList.remove('overflow-visible');
    }
}

/**
 * Adds the "overflow" class to the .carousel-inner element.
 *
 * This should be called after the slide transition completes.
 *
 * @param {HTMLElement} carouselInner - The .carousel-inner element
 */
export function setCarouselOverflow(carouselInner) {
    if (carouselInner) {
        carouselInner.classList.add('overflow-visible');
    }
}
