import { createModal } from '../modal.js'

document.addEventListener('click', async (event) => {
  // Determine the button element with data-description-file attribute
  const btn = event.target.closest('button[data-description-file]');
  if (!btn) return;

  // Read the filename from the attribute, e.g., "masse.html"
  const file = btn.getAttribute('data-description-file');

  // Get data-label from the closest .carousel-item, or use 'Description' as default
  const label = btn.closest('.carousel-item')?.getAttribute('data-label') || 'Description';

  try {
    // Build the base URL (adjust to your environment)
    const baseUrl = window.location.origin
      + '/wp-content/themes/bsawesome/html/configurator/';
    const url = baseUrl + file;

    // Fetch the file
    const response = await fetch(url);
    if (!response.ok) {
      throw new Error(`Error loading the file: ${response.status} ${response.statusText}`);
    }
    const html = await response.text();

    // Dynamically create the modal
    createModal({
      title: label,
      body: html,
      size: 'lg',
      dismiss: 'Schlieen'
    });

  } catch (err) {
    console.error('Error displaying the description:', err);
    // Fallback modal
    createModal({
      title: 'Error',
      body: 'Beschreibung konnte nicht geladen werden.'
    });
  }
});
