import {
    dependenciesValuesXvalues,
    dependenciesValuesXcontainer,
} from './../dependencies.js';

import {
    isBadspiegel,
    isLOR,
    isLR,
    isOU
} from './../variables.js';

document.addEventListener('DOMContentLoaded', () => {
    //*********************************/
    // Dependencies: Values X Values
    //*********************************/
    dependenciesValuesXvalues(
        "ambientelicht_lichtfarbe",
        value => {
            return (
                value.includes("warm_bis_kalt") ||
                value.includes("rgb") ||
                value.includes("philips_hue")
            );
        },
        "ambientelicht_bedienung",
        value => {
            return (
                value === "ohne_extra_schalter"
            );
        },
    );
    dependenciesValuesXvalues(
        "ambientelicht_lichtfarbe",
        value => {
            return (
                value.includes("warm_und_kalt") ||
                value.includes("warm_bis_kalt") ||
                value.includes("rgb") ||
                value.includes("philips_hue")
            );
        },
        "ambientelicht_bedienung",
        value => {
            return (
                value === "ohne_extra_schalter" ||
                value === "extra_kippschalter" ||
                value === "extra_gestensensor" ||
                value === "extra_touch_sensor" ||
                value === "extra_touch_sensor_inkl_dimmfunktion" ||
                value === "extra_gestensteuerung_inkl_dimmfunktion"
            );
        },
    );
    dependenciesValuesXvalues(
        "ambientelicht_lichtfarbe",
        value => {
            return (
                value === "warmweiss" ||
                value === "neutralweiss" ||
                value === "kaltweiss" ||
                value.includes("warm_und_kalt") ||
                value.includes("rgb") ||
                value.includes("philips_hue")
            );
        },
        "ambientelicht_bedienung",
        value => {
            return (
                value === "extra_fernbedienung" ||
                value === "extra_gestensteuerung_plus"
            );
        },
    );
    dependenciesValuesXvalues(
        "ambientelicht_lichtfarbe",
        value => {
            return (
                value === "warmweiss" ||
                value === "neutralweiss" ||
                value === "kaltweiss" ||
                value.includes("warm_und_kalt") ||
                value.includes("warm_bis_kalt") ||
                value.includes("philips_hue")
            );
        },
        "ambientelicht_bedienung",
        value => {
            return (
                value === "extra_rgb_fernbedienung"
            );
        },
    );
    dependenciesValuesXvalues(
        "ambientelicht_lichtfarbe",
        value => {
            return (
                value === "warmweiss" ||
                value === "neutralweiss" ||
                value === "kaltweiss" ||
                value.includes("warm_und_kalt") ||
                value.includes("warm_bis_kalt") ||
                value.includes("rgb")
            );
        },
        "ambientelicht_bedienung",
        value => {
            return (
                value === "philips_hue_app"
            );
        },
    );
    dependenciesValuesXvalues(
        "ambientelicht_lichtfarbe",
        value => {
            return (
                value.includes("warm_bis_kalt") ||
                value.includes("rgb") ||
                value.includes("philips_hue")
            );
        },
        "ambientelicht_bedienung",
        value => {
            return (
                value === "extra_schalter" ||
                value === "extra_drehdimmer"
            );
        },
    );
    dependenciesValuesXvalues(
        "ambientelicht_bedienung",
        value => {
            return (
                value.includes('touch_sensor')
                || value.includes('gestensteuerung')
            );
        },
        "ambientelicht_bedienung_position",
        value => {
            return (
                value.includes('linke_seite')
                || value.includes('rechte_seite')
            );
        },
    );
    //
    //  isBadspiegel
    //
    if (isBadspiegel) {
        //
        //  isLOR
        //
        if (isLOR || isLR) {
            dependenciesValuesXvalues(
                'bedienung', value => {
                    return (
                        value.includes('kippschalter')
                        || value.includes('gestensensor')
                    );
                },
                'bedienung_position', value => {
                    return (
                        value.includes('linke_seite')
                        || value.includes('rechte_seite')
                    );
                }
            );
        }
        //
        //  isOU
        //
        if (isOU) {
            dependenciesValuesXvalues(
                'bedienung', value => {
                    return (
                        value.includes('kippschalter')
                        || value.includes('gestensensor')
                    );
                },
                'bedienung_position', value => {
                    return (
                        value.includes('unten-mittig')
                        || value.includes('unten-links')
                        || value.includes('unten-rechts')
                    );
                }
            );
        }
    }
    //*********************************/
    // Dependencies: Values X Container
    //*********************************/
    dependenciesValuesXcontainer(
        'ambientelicht', value => {
            return (
                value !== ""
            );
        },
        'ambientelicht_lichtfarbe'
    );
    dependenciesValuesXcontainer(
        'ambientelicht', value => {
            return (
                value !== ""
            );
        },
        'ambientelicht_bedienung'
    );
    dependenciesValuesXcontainer(
        'ambientelicht_bedienung', value => {
            return (
                value.includes('kippschalter')
                || value.includes('gestensensor')
                || value.includes('touch_sensor')
                || value.includes('gestensteuerung')
                || value === 'schalter'
                || value === 'drehdimmer'
            );
        },
        'ambientelicht_bedienung_position'
    );
});
