/**
 * Data links for controlling link juice (SEO) and opening links in new tabs.
 */

// Import the createModal function from your modal module
import { createModal } from './modal'; // Adjust the path accordingly

// Event listener for both 'click' and 'keypress' events on the document
document.addEventListener('click', handleEvent);
document.addEventListener('keypress', handleEvent);

/**
 * Handles both click and keypress events to detect interactions with elements that have the 'data-link' attribute.
 * @param {Event} event - The event object.
 */
function handleEvent(event) {
    // Determine the target element that has the 'data-link' attribute
    const targetElement = event.target.closest('[data-link]');
    if (!targetElement) return;

    // If the event is a keypress, only proceed if the Enter key was pressed
    if (event.type === 'keypress' && event.key !== 'Enter') return;

    handleDataLink(targetElement);
}

/**
 * Processes the element with the 'data-link' attribute.
 * @param {HTMLElement} element - The element containing the 'data-link' attribute.
 */
function handleDataLink(element) {
    if (!element.hasAttribute('data-link')) {
        console.warn('Element missing data-link attribute:', element);
        return;
    }

    // Decode the Base64-encoded URL from the 'data-link' attribute
    let dataUrl;
    try {
        dataUrl = atob(element.getAttribute('data-link'));
    } catch (e) {
        console.error('Unable to decode data-link attribute:', e);
        return;
    }

    // Validate the decoded URL
    if (!isValidUrl(dataUrl)) {
        console.error('Invalid URL:', dataUrl);
        return;
    }

    const shouldOpenInNewTab = element.hasAttribute('data-blank');

    if (shouldOpenInNewTab) {
        // Show confirmation modal before opening in a new tab
        showNewTabModal(dataUrl, () => {
            openInNewTab(dataUrl);
        });
    } else {
        // Navigate to the URL in the same tab
        window.location.href = dataUrl;
    }
}

/**
 * Validates whether a given string is a valid URL.
 * Allows relative paths and anchors.
 * @param {string} url - The URL string to validate.
 * @returns {boolean} - True if valid, false otherwise.
 */
function isValidUrl(url) {
    // Allow relative paths and anchors
    if (
        url.startsWith('/') ||
        url.startsWith('./') ||
        url.startsWith('../') ||
        url.startsWith('#')
    ) {
        return true;
    }
    // Attempt to construct a URL object to validate absolute URLs
    try {
        new URL(url);
        return true;
    } catch (e) {
        return false;
    }
}

/**
 * Opens the specified URL in a new browser tab.
 * @param {string} dataUrl - The URL to open.
 */
function openInNewTab(dataUrl) {
    const newTab = window.open(dataUrl, '_blank', 'noopener,noreferrer');
    if (newTab) newTab.focus();
    // console.error('Fehler beim ffnen des neuen Tabs. Bitte erlauben Sie Popups fr diese Website.');
}

/**
 * Displays a confirmation modal before opening a URL in a new tab.
 * Utilizes the provided createModal function.
 * @param {string} dataUrl - The URL to open if confirmed.
 * @param {Function} callback - The function to execute upon confirmation.
 */
async function showNewTabModal(dataUrl, callback) {
    try {
        await createModal({
            title: 'Hinweis',
            body: '<p>Ein neuer Tab wird geffnet. Mchten Sie fortfahren?</p>',
            showConfirm: true,
            confirmText: 'Weiter',
            onConfirm: () => {
            callback();
            },
            showCancel: true,
            cancelText: 'Abbrechen',
            // onCancel: () => {
            // console.log('Benutzer hat das ffnen des neuen Tabs abgebrochen.');
            // },
            // backdrop: 'static',
            // keyboard: false
        });
    } catch (error) {
        console.error('Fehler beim Anzeigen:', error);
    }
}
