import { createModal } from '../../modal.js';
import { initTooltips } from '../../tooltip.js';

/**
 * Funktion zum Sammeln der Konfigurationsdaten
 */
function gatherConfigData() {
    let configData = {};

    // Radios
    $('input.option-radio:checked').each(function () {
        let name = $(this).attr('name');
        let val = $(this).val();
        configData[name] = { value: val, type: 'radio' };
    });

    // Checkbox
    $('input.option-check:checked').each(function () {
        let name = $(this).attr('name');
        if (!configData[name]) {
            configData[name] = { value: [], type: 'checkbox' };
        }
        configData[name].value.push($(this).val());
    });

    // Textfelder
    $('input.option-input').each(function () {
        let name = $(this).attr('name');
        let val = $(this).val();
        configData[name] = { value: val, type: 'input' };
    });

    // Textareas
    $('textarea.option-textarea').each(function () {
        let name = $(this).attr('name');
        let val = $(this).val();
        configData[name] = { value: val, type: 'textarea' };
    });

    // Select-Felder
    $('select.option-select').each(function () {
        let name = $(this).attr('name');
        let val = $(this).val();
        configData[name] = { value: val, type: 'select' };
    });

    return configData;
}

/**
 * Click-Handler zum Speichern der Konfiguration.
 */
$('#product-configurator-configcode-save').on('click', function () {
    // Button definieren
    let $button = $(this);

    // Button deaktivieren
    $button.prop('disabled', true);

    // Spinner anzeigen
    $button.append(
        `<span class="spinner-border spinner-border-sm ms-2" role="status" aria-hidden="true" data-id="saveSpinner"></span>`
    );

    // Konfigurationsdaten sammeln
    let configData = gatherConfigData();

    // AJAX-Request an das WordPress-Backend
    $.ajax({
        url: myAjaxData.ajaxUrl,  // bereitgestellt via wp_localize_script
        method: 'POST',
        data: {
            action: 'save_config',
            security: myAjaxData.nonce,
            product_id: myAjaxData.productId,
            config_data: configData
        },
        success: function (response) {
            if (response.success) {
                let code = response.data.generated_code;
                let msg = response.data.msg;
                let tooltip = response.data.tooltip;

                createModal({
                    title: 'Erfolg',
                    body: `
                            <p class="">${msg}</p>
                            <button type="button"
                                    class="btn btn-outline-dark"
                                    data-copy="clipboard"
                                    data-voucher="${code}"
                                    data-bs-tooltip="false"
                                    data-bs-title="${tooltip}">
                                ${code}
                                <i class="fa-sharp fa-light fa-copy" aria-hidden="true"></i>
                            </button>
                        `,
                    onShown: function () {
                        initTooltips();
                    }
                });
            }
        },
        complete: function () {
            $button.find('[data-id="saveSpinner"]').remove(); // Spinner entfernen
            $button.prop('disabled', false); // Button reaktivieren
        }
    });
});
