/**
 * ---------------------------------------------
 * Debounce
 * ---------------------------------------------
 */
export function debounce(fn, delay = 200) {
    let timeout;
    return (...args) => {
        clearTimeout(timeout);
        timeout = setTimeout(() => fn(...args), delay);
    };
}

/**
 * ---------------------------------------------
 * Parse safely strings into numbers
 * @returns {number} ? 0 for NaN
 * ---------------------------------------------
 */
export function toNumber(value) {
    // Replace comma with a dot and remove all non-digit or non-dot characters
    const sanitized = value?.replace(',', '.').replace(/[^\d.]/g, '') || '';
    const number = parseFloat(sanitized);
    return isNaN(number) ? 0 : number;
}

/**
 * ---------------------------------------------
 * Rounds a number up to the next multiple of `step`.
 * @param {number} value - The value to round up
 * @param {number} step - The step to round up to (default: 100)
 * @returns {number} The rounded value
 * ---------------------------------------------
 */
export function roundUp(value, step = 100) {
    return Math.ceil(value / step) * step;
}

/**
 * ---------------------------------------------
 * Format price to "99,90 "
 * (returns an empty string if invalid or 0)
 * ---------------------------------------------
 */
export function formatPrice(price) {
    const numericPrice = toNumber(String(price));
    return numericPrice === 0 ? '' : `${numericPrice.toFixed(2).replace('.', ',')} `;
}

/**
 * ---------------------------------------------
 * Get product title on product pages
 * (returns an empty string if not found)
 * ---------------------------------------------
 */
export function getProductTitle() {
    const titleEl = document.querySelector('.product-titel') || document.querySelector('.product_title');
    return titleEl ? titleEl.textContent.trim() : '';
}

/**
 * ---------------------------------------------
 * Get product price on product pages
 * ---------------------------------------------
 */
export function getProductPrice() {
    const priceEl = document.querySelector('.product-price') || document.querySelector('.price');
    return priceEl ? toNumber(priceEl.textContent.trim()) : 0;
}

/**
 * ---------------------------------------------
 * Updates the select element and triggers a "change" event.
 * ---------------------------------------------
 */
export function updateSelectAndTrigger(select, option) {
    select.value = option.value;
    select.dispatchEvent(new Event('change'));
}