/**
 * Hilfsfunktion: Suche nach sichtbaren Input-Elementen innerhalb einer Button-Gruppe
 */
const getVisibleInput = (inputs, containerSelector = '.btn-group') => {
    const visibleInput = inputs.find(input => {
        const container = input.closest(containerSelector);
        return container && !container.classList.contains("d-none");
    });

    // console.log(`getVisibleInput: Found visible input?`, !!visibleInput);
    return visibleInput;
};

/**
 * Hilfsfunktion: Fhre den Fokus-Effekt auf einem Container aus
 */
function applyFocusEffect(container, focusClass) {
    const offcanvasToggle = container.querySelector("[data-bs-toggle='offcanvas']"); 
    const numberInput = container.querySelector('input[type="number"]');

    if (!offcanvasToggle && !numberInput) {
        console.warn(`applyFocusEffect: No offcanvas toggle found in container`);
    }

    // console.log(`applyFocusEffect: Applying focus effect to container`);
    setTimeout(() => {
        offcanvasToggle.classList.add("focus", "focus-ring", focusClass);
        offcanvasToggle.style.borderColor = "var(--bs-secondary)";
    }, 300);
    setTimeout(() => {
        offcanvasToggle.classList.remove("focus", "focus-ring", focusClass);
        offcanvasToggle.style.borderColor = "";
    }, 3300);
}

/**
 * Dependencies: Values X Values
 *
 * Abhngigkeit: Basierend auf dem Wert eines Trigger-Inputs wird ein Satz von Ziel-Inputs (gefiltert durch targetValueCondition)
 * entweder angezeigt oder ausgeblendet. Wird die Abhngigkeit erfllt, so wird versucht, den ersten sichtbaren, gltigen Ziel-Input auszuwhlen.
 */
export function dependenciesValuesXvalues(triggerName, condition, targetOptionName, targetValueCondition) {
    const triggerInputs = document.querySelectorAll(`input[name="${triggerName}"]`);
    // console.log(`dependenciesValuesXvalues: Found ${triggerInputs.length} trigger inputs for ${triggerName}`);

    // Filtern der Ziel-Inputs anhand targetValueCondition
    const targetInputs = Array.from(document.querySelectorAll(`input[name="${targetOptionName}"]`))
        .filter(input => targetValueCondition(input.value));

    // console.log(`dependenciesValuesXvalues: Found ${targetInputs.length} target inputs for ${targetOptionName}`);

    if (!targetInputs.length) {
        // console.warn(`dependenciesValuesXvalues: No target inputs found for ${targetOptionName}`);
        return;
    }

    const handleChange = function () {
        const dependency = condition(this.value);
        // console.log(`dependenciesValuesXvalues: Trigger input changed. Dependency met?`, dependency);

        // Passe die Sichtbarkeit der Container der gefilterten Ziel-Inputs an
        targetInputs.forEach(targetInput => {
            const container = targetInput.closest(".btn-group");
            if (!container) {
                console.warn(`dependenciesValuesXvalues: No container found for target input`);
                return;
            }
            container.classList.toggle("d-none", dependency);
            // console.log(`dependenciesValuesXvalues: Toggled visibility of container for input ${targetInput.value}`);
        });

        // Wenn Abhngigkeit erfllt ist, versuche einen gltigen Input auszuwhlen
        if (dependency) {
            const allTargetInputs = Array.from(document.querySelectorAll(`input[name="${targetOptionName}"]`));
            // console.log(`dependenciesValuesXvalues: Found ${allTargetInputs.length} total target inputs for ${targetOptionName}`);

            if (!allTargetInputs.length) {
                // console.warn(`dependenciesValuesXvalues: No inputs found for ${targetOptionName}`);
                return;
            }

            // Prfe, ob bereits ein gltiger (ausgewhlter und sichtbarer) Input vorhanden ist
            const currentSelected = allTargetInputs.find(input => input.checked);
            if (currentSelected) {
                const currentContainer = currentSelected.closest('.btn-group, .option-group');
                if (currentContainer && !currentContainer.classList.contains('d-none')) {
                    // console.log(`dependenciesValuesXvalues: Already selected input ${currentSelected.value} is visible`);
                    return;
                }
            }

            // Whle den ersten sichtbaren Input aus
            const firstVisibleInput = getVisibleInput(allTargetInputs);
            if (firstVisibleInput) {
                firstVisibleInput.checked = true;
                firstVisibleInput.dispatchEvent(new Event("change", { bubbles: true }));
                // console.log(`dependenciesValuesXvalues: Selected first visible input ${firstVisibleInput.value}`);

                const targetContainer = firstVisibleInput.closest('.option-group');
                if (targetContainer) {
                    applyFocusEffect(targetContainer, "focus-ring-secondary");
                    return;
                }
            } else {
                // console.warn(`dependenciesValuesXvalues: No visible input found for ${targetOptionName}`);
                return;
            }
        }
    };

    triggerInputs.forEach(input => input.addEventListener("change", handleChange));
}

/**
 * Dependencies: Values X Container
 *
 * Abhngigkeit: Basierend auf dem Wert eines Trigger-Inputs wird ein Container (targetContainer)
 * entweder angezeigt (und ggf. ein gltiger Input innerhalb des Containers selektiert) oder ausgeblendet.
 */
export function dependenciesValuesXcontainer(triggerName, condition, targetIdSuffix) {
    const triggerInputs = document.querySelectorAll(`input[name="${triggerName}"]`);
    console.log(`dependenciesValuesXcontainer: Found ${triggerInputs.length} trigger inputs for ${triggerName}`);

    const targetContainer = document.querySelector(`#option_${targetIdSuffix}`);
    if (!targetContainer) {
        console.warn(`dependenciesValuesXcontainer: Container #option_${targetIdSuffix} not found`);
        return;
    }

    /**
     * Zeige den Container und setze ggf. den Fokus-Effekt
     */
    const showContainer = (focusClass = null) => {
        targetContainer.classList.remove("d-none");
        // console.log(`dependenciesValuesXcontainer: Showing container #option_${targetIdSuffix}`);
        if (focusClass) {
            applyFocusEffect(targetContainer, focusClass);
        }
    };

    const handleChange = function () {
        const dependency = condition(this.value);
        // console.log(`dependenciesValuesXcontainer: Trigger input changed. Dependency met?`, dependency);

        if (dependency) {
            const optiongroupnumber = targetContainer.closest('.option-group');

            if (optiongroupnumber && optiongroupnumber.classList.contains('option-group-number')) {
                targetContainer.classList.remove("d-none");

                // Selektiere den number-Input gezielt, falls vorhanden:
                const numberInput = targetContainer.querySelector('input[type="number"]');
                if (numberInput) {
                    const minVal = numberInput.getAttribute('min');
                    if (minVal && numberInput.value.trim() === "") {
                        numberInput.value = minVal;
                        numberInput.classList.remove("no-input"); // Sollte eigentlich automatisch von der anderen Funktion aus hinzugefgt werden
                        numberInput.classList.add("yes-input"); // Sollte eigentlich automatisch von der anderen Funktion aus hinzugefgt werden
                        // console.log(`dependenciesValuesXcontainer: Number input - set value to min: ${minVal}`);
                        setTimeout(() => { // Muss auch durch die Hilfsfunktion getriggert werden am besten
                            numberInput.classList.add("focus", "focus-ring", "focus-ring-secondary");
                            numberInput.style.borderColor = "var(--bs-secondary)";
                        }, 300);
                        setTimeout(() => {
                            numberInput.classList.remove("focus", "focus-ring", "focus-ring-secondary");
                            numberInput.style.borderColor = "";
                        }, 3300);
                    }
                }
            }

            const allTargetInputs = Array.from(targetContainer.querySelectorAll('input'));
            // console.log(`dependenciesValuesXcontainer: Found ${allTargetInputs.length} target inputs in container`);

            const checkedInput = allTargetInputs.find(input => input.checked);

            // Falls ein gltiger Input bereits ausgewhlt wurde, nichts weiter tun
            if (checkedInput && checkedInput.value.trim() !== "") {
                // console.log(`dependenciesValuesXcontainer: Already selected input ${checkedInput.value} is valid`);
                return;
            }

            // Falls ein Input in einer "normalen" (nicht-child) option-group checked ist, Container anzeigen
            if (checkedInput) {
                const optiongroup = checkedInput.closest('.option-group');
                if (optiongroup && !optiongroup.classList.contains('option-group-child')) {
                    // console.log(`dependenciesValuesXcontainer: Found checked input in non-child option-group`);
                    showContainer("focus-ring-secondary");
                    return;
                }
            }

            // Suche den ersten sichtbaren, nicht-leeren Input
            const firstValidInput = allTargetInputs.find(input => {
                const btnGroup = input.closest('.btn-group');
                return input.value.trim() !== "" && btnGroup && !btnGroup.classList.contains('d-none');
            });

            if (firstValidInput) {
                const optiongroup = firstValidInput.closest('.option-group');
                if (optiongroup && !optiongroup.classList.contains('option-group-child')) {
                    // console.log(`dependenciesValuesXcontainer: Found valid input in non-child option-group`);
                    showContainer("focus-ring-secondary");
                    return;
                }
                firstValidInput.checked = true;
                firstValidInput.dispatchEvent(new Event("change", { bubbles: true }));
                // console.log(`dependenciesValuesXcontainer: Selected first valid input ${firstValidInput.value}`);
                showContainer("focus-ring-secondary");
            } else {
                // console.warn(`dependenciesValuesXcontainer: No visible input with a valid (non-empty) value found for ${targetIdSuffix}`);
            }
        } else {
            // Abhngigkeit nicht erfllt: Container ausblenden und leeren Input selektieren
            targetContainer.classList.add("d-none");
            // console.log(`dependenciesValuesXcontainer: Hiding container #option_${targetIdSuffix}`);

            const optiongroupnumber = targetContainer.closest('.option-group');
            if (optiongroupnumber && optiongroupnumber.classList.contains('option-group-number')) {
                const numberInput = targetContainer.querySelector('input[type="number"]');
                if (numberInput) {
                    numberInput.value = "";
                    // console.log(`dependenciesValuesXcontainer: Number input - cleared value`);
                }
            }

            const emptyInput = targetContainer.querySelector('input[value=""]');
            if (emptyInput) {
                emptyInput.checked = true;
                emptyInput.dispatchEvent(new Event("change", { bubbles: true }));
                // console.log(`dependenciesValuesXcontainer: Selected empty input`);
            }
        }
    };

    triggerInputs.forEach(input => input.addEventListener("change", handleChange));
}

/**
 * Dependencies: Cleanings
 *
 * Entfernt Button-Gruppen fr gegebene Namen und Werte.
 */
export function dependenciesCleaningValues(name, values) {
    // console.log(`dependenciesCleaningValues: Cleaning values for ${name}`);
    values.forEach(value => {
        const elements = document.querySelectorAll(`input[name="${name}"][value="${value}"]`);
        // console.log(`dependenciesCleaningValues: Found ${elements.length} elements for ${name}: ${value}`);
        elements.forEach(el => {
            const btnGroup = el.closest('.btn-group');
            if (btnGroup) {
                btnGroup.remove();
                // console.log(`dependenciesCleaningValues: Removed btn-group for ${name}: ${value}`);
            } else {
                // console.warn(`dependenciesCleaningValues: Could not find btn-group for ${name}: ${value}`);
            }
        });
    });
}