import {
    dependenciesValuesXcontainer,
    dependenciesValuesXvalues,
} from './../dependencies';

import {
} from '../variables.js';

document.addEventListener('DOMContentLoaded', () => {
    //*********************************/
    // Dependencies: Values X Values
    //*********************************/
    dependenciesValuesXvalues(
        "spiegelheizung_bedienung",
        value => {
            return (
                value.includes('touch_sensor')
            );
        },
        "spiegelheizung_bedienung_position",
        value => {
            return (
                value.includes('linke_seite')
                || value.includes('rechte_seite')
            );
        },
    );
    //*********************************/
    // Dependencies: Values X Container
    //*********************************/
    dependenciesValuesXcontainer(
        'spiegelheizung', value => {
            return (
                value === "ja");
        },
        'spiegelheizung_bedienung'
    );
    dependenciesValuesXcontainer(
        'spiegelheizung_bedienung', value => {
            return (
                value !== ""
            );
        },
        'spiegelheizung_bedienung_position'
    );
});
