import {
    offcanvasElements,
} from './variables';

/* ------------------------------------------------------------------
 * updateOffcanvasButton
 * Updates the text in .option-value-label (outside the button but 
 * within the .form-floating container).
 *
 * 1) If the "none" radio (value="") is selected, clears the text.
 * 2) Otherwise, displays the label and optional price.
 * ------------------------------------------------------------------ */
export function updateOffcanvasButton(offcanvas, button) {

    const radioSelected = offcanvas.querySelector("input[type='radio']:checked");
    if (!radioSelected)
        return;

    const radioGroup = button.closest(".option-group");
    if (!radioGroup)
        return;

    const radioButtonValueLabel = radioGroup.querySelector(".option-value-label");
    if (!radioButtonValueLabel)
        return;

    // Identify the radio with value="", then get its label element, which contains class "fade".
    let radioNoneValueLabel = null;
    const radioNoneValue = offcanvas.querySelector("input[type='radio'][value='']");
    if (radioNoneValue) {
        radioNoneValueLabel = radioNoneValue.nextElementSibling;
    }

    // Case: No selection (value="")
    if (!radioSelected || radioSelected.value === "") {
        button.classList.remove("yes-selection");
        button.classList.add("no-selection");
        radioButtonValueLabel.classList.remove("show");
        radioButtonValueLabel.innerHTML = "";

        // Hide "No selection" button inside offcanvas
        // if (radioNoneValueLabel) {
        //     radioNoneValueLabel.classList.remove("show");
        // }

        return;
    }

    // Case: Valid selection
    if (radioSelected && radioSelected.value !== "") {
        const labelText = radioSelected.dataset.label || "";
        const priceText = radioSelected.dataset.price || "0";

        // Parse price to float for comparison
        const priceValue = parseFloat(priceText);

        // Bedingtes Hinzufgen des Preis-Spans
        let priceSpan = "";
        if (priceValue > 0) {
            priceSpan = `<span class="col-auto">(+${priceText} )</span>`;
        }

        radioButtonValueLabel.innerHTML = `
            <span class="row align-items-center g-1">
                <span class="col text-truncate">${labelText}</span>
                ${priceSpan}
            </span>
        `;
        radioButtonValueLabel.classList.add("show");

        // Show "No selection" button inside offcanvas
        // if (radioNoneValueLabel) {
        //     radioNoneValueLabel.classList.add("show");
        // }

        button.classList.add("yes-selection");
        button.classList.remove("no-selection");

        return;
    }
}

/* ------------------------------------------------------------------
 * Offcanvas events (for visual classes "on-selection")
 * ------------------------------------------------------------------ */
function onOffcanvasButton(event) {
    const offcanvas = event.target;

    if (!offcanvas.classList.contains("option-offcanvas")) {
        return;
    }

    const offcanvasId = offcanvas.id;
    const button = document.querySelector(`button[data-bs-target='#${offcanvasId}']`);

    if (!button)
        return;

    if (event.type === "show.bs.offcanvas") {
        button.classList.add("on-selection");
    } else if (event.type === "hide.bs.offcanvas") {
        button.classList.remove("on-selection");
    }
}
document.addEventListener("show.bs.offcanvas", onOffcanvasButton);
document.addEventListener("hide.bs.offcanvas", onOffcanvasButton);

/* ------------------------------------------------------------------
 * Global listener for any radio input changes.
 * Closes the Offcanvas automatically when a new option is selected.
 * ------------------------------------------------------------------ */

// Event listener for changes

document.addEventListener("change", (event) => {
    const changedElement = event.target;

    // Check if the changed element is a radio button inside an .option-offcanvas
    if (
        changedElement.matches("input[type='radio']") &&
        changedElement.closest(".option-offcanvas")
    ) {
        const offcanvas = changedElement.closest(".option-offcanvas");
        if (!offcanvas) return;

        // Find the button that triggers this Offcanvas
        const offcanvasId = offcanvas.id;
        const button = document.querySelector(`button[data-bs-target='#${offcanvasId}']`);
        if (!button) return;

        // Update button text
        updateOffcanvasButton(offcanvas, button);

        // Automatically close the Offcanvas if it is open
        const offcanvasInstance = bootstrap.Offcanvas.getInstance(offcanvas);
        if (offcanvasInstance) {
            offcanvasInstance.hide();
        }
    }
});

// Init after site load (DOMContentLoaded)

document.addEventListener("DOMContentLoaded", () => {
    offcanvasElements.forEach(offcanvas => {
        const offcanvasId = offcanvas.id;
        const button = document.querySelector(`button[data-bs-target='#${offcanvasId}']`);
        if (button) {
            updateOffcanvasButton(offcanvas, button);
        }
    });
});
