import {
    dependenciesValuesXcontainer,
    dependenciesValuesXvalues,
} from './../dependencies';

import {
} from '../variables.js';

document.addEventListener('DOMContentLoaded', () => {
    //*********************************/
    // Dependencies: Values X Values
    //*********************************/
    dependenciesValuesXvalues(
        'steckdose', value => {
            return (
                value === '1x'
            );
        },
        'steckdose_position', value => {
            return (
                value === 'links_und_rechts'
            );
        }
    );
    //*********************************/
    // Dependencies: Values X Container
    //*********************************/
    dependenciesValuesXcontainer(
        'steckdose', value => {
            return (
                value !== ""
            );
        },
        'steckdose_farbe'
    );
    dependenciesValuesXcontainer(
        'steckdose', value => {
            return (
                value !== ""
            );
        },
        'steckdose_position'
    );
});