import { createModal } from '../../modal.js';

/**
 * Applies configuration data to the form.
 * Returns an array of fields that could not be mapped (notFound).
 *
 * @param {Object} configData - The configuration data object (e.g., { fieldName: { type, value }, ... }).
 * @returns {Array} - An array of objects representing fields that were not successfully applied.
 */
function applyConfigData(configData) {
    // Array for collecting fields that could not be found or applied
    const notFound = [];

    /**
     * 1) Reset all relevant fields (inputs, checkboxes, selects) before applying new values
     */
    $('input.option-radio, input.option-check').prop('checked', false);
    $('input.option-input, textarea.option-textarea').val('');
    $('select.option-select').prop('selectedIndex', 0);

    /**
     * 2) Iterate through configData to apply each field's value.
     */
    $.each(configData, (fieldName, fieldObj) => {
        const { type: fieldType, value: fieldValue } = fieldObj;

        switch (fieldType) {
            case 'radio': {
                const $radio = $(`input[type="radio"][name="${fieldName}"][value="${fieldValue}"]`);
                if ($radio.length) {
                    $radio.prop('checked', true);

                    // Optionally trigger a native 'change' event
                    const domRadio = $radio[0];
                    if (domRadio) {
                        domRadio.dispatchEvent(new Event('change', { bubbles: true }));
                    }
                } else {
                    notFound.push({ fieldName, fieldType, fieldValue });
                }
                break;
            }

            case 'input': {
                const $input = $(`input[name="${fieldName}"]`);
                if ($input.length) {
                    $input.val(fieldValue);
                    // Optionally trigger a jQuery change event
                    // $input.trigger('change');
                } else {
                    notFound.push({ fieldName, fieldType, fieldValue });
                }
                break;
            }

            case 'select': {
                const $select = $(`select[name="${fieldName}"]`);
                if ($select.length) {
                    $select.val(fieldValue);
                    // Optionally trigger a jQuery change event
                    // $select.trigger('change');
                } else {
                    notFound.push({ fieldName, fieldType, fieldValue });
                }
                break;
            }

            default: {
                console.warn('Unknown fieldType:', fieldType, fieldName, fieldValue);
                notFound.push({ fieldName, fieldType: 'unknown', fieldValue });
                break;
            }
        }
    });

    // Return an array of fields that were not applied
    return notFound;
}

/**
 * Applies the configuration (and handles mismatch warnings) in one place.
 *
 * @param {Object}  configData       - The config_data object from the server.
 * @param {String}  successMsg       - The success message from the server.
 * @param {Number}  remoteProductId  - The product ID returned from the server.
 * @param {Number}  currentProductId - The current product ID in the frontend.
 */
function doApply(configData, successMsg, remoteProductId, currentProductId) {
    // Check if product IDs mismatch
    if (currentProductId && remoteProductId && remoteProductId !== currentProductId) {
        // Show a warning modal and ask the user to confirm
        showValidationModal(() => {
            // If user confirms, apply config for a different product
            showSuccessModal(configData, successMsg, true);
        });
    } else {
        // If no mismatch, proceed directly
        showSuccessModal(configData, successMsg, false);
    }
}

/**
 * Displays a validation modal when the loaded product ID does not match the current product ID.
 * The callback (onConfirm) is invoked if the user chooses to continue loading.
 *
 * @param {Function} onConfirm - A callback function to be executed if the user clicks "Ja, trotzdem laden".
 */
function showValidationModal(onConfirm) {
    createModal({
        title: 'Abweichendes Produkt',
        body: `
            <div class="alert alert-warning d-flex align-items-center mb-0" role="alert">
                <i class="fa-sharp fa-light fa-exclamation-triangle me-3"></i>
                <div>Der geladene Code gehrt zu einem anderen Produkt. Mchtest du die Konfiguration trotzdem laden?</div>
            </div>
        `,
        showConfirm: true,
        confirmText: 'Ja, trotzdem laden',
        showCancel: true,
        cancelText: 'Abbrechen',
        onConfirm: onConfirm
    });
}

/**
 * Displays the final success modal.
 * If forcedLoad === true, we also indicate that the product was different.
 * Additionally, we call applyConfigData(configData) and show a list of fields not mapped.
 */
function showSuccessModal(configData, successMsg, forcedLoad) {
    // Here we apply the configuration data to the form.
    const notFoundFields = applyConfigData(configData);

    let modalBody = '';

    // If we want to highlight that it's for a different product
    if (forcedLoad) {
        modalBody += `
            <div class="alert alert-info d-flex align-items-center mb-0" role="alert">
                <i class="fa-sharp fa-light fa-check me-3"></i>
                <div>Du hast die Konfiguration eines anderen Produkts geladen.</div>
            </div>
        `;
    } else {
        modalBody += `
            <div class="alert alert-success d-flex align-items-center mb-0" role="alert">
                <i class="fa-sharp fa-light fa-check me-3"></i>
                <div>${successMsg}</div>
            </div>
        `;
    }

    // Falls du irgendwann nicht gemappte Felder anzeigen mchtest, kannst du den auskommentierten
    // Code wieder aktivieren.

    createModal({
        title: 'Konfiguration angewendet',
        body: modalBody,
        showConfirm: false,
        showCancel: false
    });
}

/**
 * Displays an error modal with a Bootstrap alert inside it.
 *
 * @param {String} errorMsg - The error message to display.
 * @param {String} [customTitle='Fehler'] - Optional custom title for the modal.
 */
function showErrorModal(errorMsg, customTitle = 'Fehler') {
    const modalBody = `
        <div class="alert alert-danger d-flex align-items-center mb-0" role="alert">
            <i class="fa-sharp fa-light fa-exclamation-triangle me-3"></i>
            <div>${errorMsg}</div>
        </div>
    `;

    createModal({
        title: customTitle,
        body: modalBody,
        showConfirm: false,
        showCancel: false
    });
}

/**
 * Wenn Enter in #product-configurator-configcode-input gedrckt wird,
 * soll statt des Form-Submits das Klicken auf #product-configurator-configcode-load ausgelst werden
 */
$('#product-configurator-configcode-input').on('keydown', function (event) {
    if (event.key === 'Enter' || event.keyCode === 13) {
        event.preventDefault();
        $('#product-configurator-configcode-load').trigger('click');
    }
});

/**
 * Click handler for loading a configuration via config code.
 * Sends an AJAX request to the server and handles the response.
 */
$('#product-configurator-configcode-load').on('click', function () {
    const $button = $(this);
    const code = $('#product-configurator-configcode-input').val().trim();

    // Show spinner and disable button
    $button.append(`
        <span class="spinner-border spinner-border-sm ms-2" role="status" aria-hidden="true" id="loadSpinner"></span>
    `);
    $button.prop('disabled', true);

    $.ajax({
        url: myAjaxData.ajaxUrl,  // Provided via wp_localize_script in PHP
        method: 'POST',
        data: {
            action: 'load_config',
            security: myAjaxData.nonce,
            config_code: code
        },
        success: function (response) {
            if (response.success) {
                const { msg, product_id, config_data } = response.data;
                const remoteProductId = parseInt(product_id, 10) || null;

                // Determine current product ID from myAjaxData (if available)
                let currentProductId;
                if (typeof myAjaxData !== 'undefined' && myAjaxData.productId) {
                    const parsedId = parseInt(myAjaxData.productId, 10);
                    if (!isNaN(parsedId)) {
                        currentProductId = parsedId;
                    }
                }

                // Delegate everything to doApply
                doApply(config_data, msg, remoteProductId, currentProductId);

            } else {
                // Server returned an error (wp_send_json_error)
                // Typically { msg: 'No code provided.', ... }
                const errorMsg = response.data && response.data.msg
                    ? response.data.msg
                    : 'Unbekannter Fehler';

                showErrorModal(errorMsg);
            }
        },
        error: function (xhr, status, error) {
            // Network or 500 server error
            let errorMsg = error;
            if (xhr.responseJSON && xhr.responseJSON.data && xhr.responseJSON.data.msg) {
                errorMsg = xhr.responseJSON.data.msg;
            }

            showErrorModal(errorMsg, 'AJAX Fehler');
        },
        complete: function () {
            // Remove spinner and re-enable button
            $button.find('#loadSpinner').remove();
            $button.prop('disabled', false);
        }
    });
});
