// Delegate clicks on dynamically loaded note items via event bubbling
document.addEventListener('click', function(event) {
    // Find the closest note item ancestor of the clicked element
    const noteItem = event.target.closest('.wcpf-note-item');
    if (!noteItem) return; // Click did not occur within a note item
  
    // If the click was directly on the remove filter button, do nothing to avoid duplicate actions
    if (event.target.classList.contains('remove-filter')) return;
  
    // Locate the remove filter button inside this note item
    const removeButton = noteItem.querySelector('.remove-filter');
    if (removeButton) {
      // Programmatically trigger the click on the remove filter button
      removeButton.click();
    }
  });
  