/**
 * Quantity input gield plus/minus buttons
 */

document.addEventListener('DOMContentLoaded', function () {
    // 1. Plus/Minus Click Logic
    document.body.addEventListener('click', function (event) {
        // Check if the clicked element is a button with class 'plus' or 'minus'
        if (event.target.matches('button.plus, button.minus')) {
            const button = event.target;
            const quantityElement = button.closest('.quantity');
            if (!quantityElement) return; // Exit if no parent .quantity element is found

            const qtyInput = quantityElement.querySelector('.qty');
            if (!qtyInput) return; // Exit if no input field with class .qty is found

            // Get current value, max, min, and step attributes
            let val = parseFloat(qtyInput.value) || 0;
            let max = qtyInput.getAttribute('max');
            max = max !== null ? parseFloat(max) : '';
            let min = qtyInput.getAttribute('min');
            min = min !== null ? parseFloat(min) : 0;
            let step = qtyInput.getAttribute('step');
            step = step !== null ? parseFloat(step) : 1;

            if (button.classList.contains('plus')) {
                // Plus Button Logic
                if (max !== '' && max <= val) {
                    qtyInput.value = max;
                } else {
                    qtyInput.value = val + step;
                }
            } else {
                // Minus Button Logic
                if (min !== '' && min >= val) {
                    qtyInput.value = min;
                } else if (val > min) { // Changed condition to use 'min' instead of hardcoded '1'
                    qtyInput.value = val - step;
                }
            }

            // Trigger Cart Update with a slight delay to prevent focus issues
            const form = button.closest('form.woocommerce-cart-form');
            if (form) {
                const updateBtn = form.querySelector('button[name="update_cart"]');
                if (updateBtn) {
                    updateBtn.disabled = false; // Enable the update button
                    // Use setTimeout to defer the click event, preventing potential focus conflicts
                    setTimeout(() => {
                        updateBtn.click(); // Simulate a click on the update button
                    }, 0);
                }
            }
        }
    });

    // 2. Store Original Value on Focus
    document.body.addEventListener('focusin', function (event) {
        const target = event.target;
        if (target.matches('.quantity .qty')) {
            target.dataset.originalValue = target.value; // Store the original value in a data attribute
        }
    });

    // 3. Compare Values on Blur (Focus Out)
    document.body.addEventListener('focusout', function (event) {
        const target = event.target;
        if (target.matches('.quantity .qty')) {
            const originalValue = target.dataset.originalValue;
            const currentValue = target.value;

            // Update only if the value has changed
            if (originalValue !== currentValue) {
                const form = target.closest('form.woocommerce-cart-form');
                if (form) {
                    const updateBtn = form.querySelector('button[name="update_cart"]');
                    if (updateBtn) {
                        updateBtn.disabled = false; // Enable the update button
                        // Use setTimeout to defer the click event, preventing potential focus conflicts
                        setTimeout(() => {
                            updateBtn.click(); // Simulate a click on the update button
                        }, 0);
                    }
                }
            }
        }
    });
});
