
// events.js

import {
    configurator,
    configuratorCarousel,
    configuratorCarouselInner,
    configuratorProgress,
    configuratorProgressbar,
    configuratorTotalSteps,
    configuratorPrev,
    configuratorNext,
    configuratorCurrentStep,
    durchmesserInput,
    breiteInput,
    hoeheInput,
    isSK2
} from './variables';

import {
    updateStep,
    scrollToCarouselTop,
    setCarouselHeight,
    resetCarouselHeight,
    removeCarouselOverflow,
    setCarouselOverflow
} from './carousel.js';

import {
    updateSummary
} from './summary';

import {
    calcPrice1x100,
    calcPrice2x100
} from './pricecalcs/pricematrices';

/* ------------------------------------------------------------------
 * Events @ content loaded.
 * ------------------------------------------------------------------ */

document.addEventListener('DOMContentLoaded', () => {
    // Configurator Carousel
    if (configuratorCarousel) {
        // Initialize step 1
        updateStep(1,
            configuratorTotalSteps,
            configuratorProgressbar,
            configuratorCurrentStep,
            configuratorPrev,
            configuratorNext);

        // Set overflow class
        setCarouselOverflow(configuratorCarouselInner);
    }

    // Dispatch change event for all radio inputs inside configurator
    const optionRadios = document.querySelectorAll('input.option-radio:checked');
    if (optionRadios.length > 0) {
        optionRadios.forEach(input => {
            input.dispatchEvent(new Event("change", { bubbles: true }));
        });
    } else {
        // console.warn('Keine Input-Elemente mit der Klasse "option-radio" gefunden.');
        return;
    }
});

/* ------------------------------------------------------------------
 * Events @ resize.
 * ------------------------------------------------------------------ */

window.addEventListener('resize', () => {

    // Configurator Carousel
    if (configuratorCarousel) {
        setCarouselHeight(configuratorCarouselInner);
        resetCarouselHeight(configuratorCarouselInner);
    }

});

/** ------------------------------------------------------------------
 *  Event @ Carousel.
 * ------------------------------------------------------------------ */

if (configuratorCarousel) {

    /* Before slide */
    configuratorCarousel.addEventListener('slide.bs.carousel', (event) => {

        // Update the height of the carousel
        setCarouselHeight(configuratorCarouselInner); // Current slide
        requestAnimationFrame(() => {
            setCarouselHeight(configuratorCarouselInner, event.relatedTarget); // Next slide
        });

        // Update the progress (bar, buttons, steps, etc.)
        const nextStep = event.to + 1; // event.to is zero-based
        updateStep(
            nextStep,
            configuratorTotalSteps,
            configuratorProgressbar,
            configuratorCurrentStep,
            configuratorPrev,
            configuratorNext
        );

        // Scroll to the top of the carousel (if it is not visible)
        scrollToCarouselTop(configurator, configuratorProgress);

        // Add new function to set "overflow"
        removeCarouselOverflow(configuratorCarouselInner);
    });

    /* After slide */
    configuratorCarousel.addEventListener('slid.bs.carousel', () => {
        resetCarouselHeight(configuratorCarouselInner);
        setCarouselOverflow(configuratorCarouselInner);
    });

}

/* ------------------------------------------------------------------
 * Event listener input radio, input und select.
 * ------------------------------------------------------------------ */

document.querySelectorAll('.option-radio').forEach((radioEl) => {
    radioEl.addEventListener('change', () => {
        setTimeout(updateSummary, 1); // Delay to fix pricematrice calculation
    });
});

document.querySelectorAll('.option-input').forEach((inputEl) => {
    inputEl.addEventListener('input', () => {
        setTimeout(updateSummary, 1); // Delay to fix pricematrice calculation
    });
});

/* ------------------------------------------------------------------
 * Event listener calcPrice for "durchmesser" input.
 * ------------------------------------------------------------------ */
document.addEventListener("DOMContentLoaded", function () {
    // const durchmesserInput = document.querySelector('.option-input[name="durchmesser"]');
    const pricematrixSelect = document.querySelector('.option-price[name*="pxd"]');

    // Kurze Prfung, ob die bentigten DOM-Elemente existieren
    if (!durchmesserInput || !pricematrixSelect) {
        // console.warn("[calcPrice] Relevante Elemente nicht gefunden. Skript wird nicht ausgefhrt.");
        return;
    }

    // Zustzliche Felder optional abfragen
    const breiteHoeheInput = document.querySelector('.option-input[name="breite_hoehe"]');
    const hoeheSchnittkanteInput = document.querySelector('.option-input[name="hoehe_schnittkante"]');
    const schnittkanteInput = document.querySelector('.option-input[name="schnittkante"]');

    const pricematrixCache = {};
    const lastValueCache = { lastRoundedValue: null };

    // Option-Elemente auslesen und cachen
    pricematrixSelect.querySelectorAll('option[data-label]').forEach((currentOption) => {
        const labelValue = parseInt(currentOption.dataset.label, 10);
        if (!isNaN(labelValue)) {
            pricematrixCache[String(labelValue)] = currentOption;
        }
    });

    function calcPrice() {
        // Hier wird die Berechnung durchgefhrt  du kannst innerhalb der Funktion
        // auch bei Bedarf die Werte der zustzlichen Felder (hoehe_schnittkante und schnittkante) einbeziehen.
        calcPrice1x100(durchmesserInput, pricematrixSelect, pricematrixCache, lastValueCache);
    }

    function calcPriceSK2() {
        // Hier wird die Berechnung durchgefhrt  du kannst innerhalb der Funktion
        // auch bei Bedarf die Werte der zustzlichen Felder (hoehe_schnittkante und schnittkante) einbeziehen.
        calcPrice1x100(breiteHoeheInput, pricematrixSelect, pricematrixCache, lastValueCache);
    }

    // Event-Listener hinzufgen: Bei nderung der Werte wird die Berechnung getriggert
    if (durchmesserInput && !isSK2) {
        durchmesserInput.addEventListener('input', calcPrice);
    }
    if (isSK2) {
        breiteHoeheInput.addEventListener('input', calcPriceSK2);
    }
    if (hoeheSchnittkanteInput) {
        hoeheSchnittkanteInput.addEventListener('input', calcPrice);
    }
    if (schnittkanteInput) {
        schnittkanteInput.addEventListener('input', calcPrice);
    }

    // Initiale Berechnung
    calcPrice();
});


/**
 * Event listener calcPrice2x100() for "breite" x "hhe" input.
 */
document.addEventListener('DOMContentLoaded', function () {
    // const breiteInput = document.querySelector('.option-input[name="breite"]');
    // const hoeheInput = document.querySelector('.option-input[name="hoehe"]');
    const pricematrixSelect = document.querySelector('.option-price[name*="pxbh"]');

    if (!breiteInput || !hoeheInput || !pricematrixSelect) {
        console.warn("[calcPrice] Relevante Elemente nicht gefunden. Skript wird nicht ausgefhrt.");
        return;
    }

    const pricematrixCache = {};
    const lastValueCache = { lastRoundedValue: null };

    // Option-Elemente auslesen und cachen
    pricematrixSelect.querySelectorAll('option').forEach((currentOption) => {
        if (currentOption.value.includes('x')) {
            pricematrixCache[currentOption.value] = currentOption;
        }
    });

    function calcPrice() {
        calcPrice2x100(breiteInput, hoeheInput, pricematrixSelect, pricematrixCache, lastValueCache);
    }

    breiteInput.addEventListener('input', calcPrice);
    hoeheInput.addEventListener('input', calcPrice);

    calcPrice();
});