import {
    dependenciesValuesXvalues,
    dependenciesValuesXcontainer,
} from './../dependencies.js';

document.addEventListener('DOMContentLoaded', () => {
    //*********************************/
    // Dependencies: Values X Values
    //*********************************/
    dependenciesValuesXvalues(
        "anschluesse",
        value => {
            return (
                value === "1x_steckdose" ||
                value === "1x_usb"
            );
        },
        "anschluesse_position",
        value => {
            return (
                value === "links_und_rechts"
            );
        },
    );
    //*********************************/
    // Dependencies: Values X Container
    //*********************************/
    dependenciesValuesXcontainer(
        'anschluesse',
        value => {
            return (
                value !== ''
            );
        },
        'anschluesse_position'
    );
});
