/**
 * Countdown Timer 
 * 
 * @param {*} targetDate 
 * @param {*} targetTime 
 */

function startCountdown(targetDate, targetTime) {
    var $countdown = $('#marketingCountdown');
    var $days = $countdown.find('.days');
    var $hours = $countdown.find('.hours');
    var $minutes = $countdown.find('.minutes');
    var $seconds = $countdown.find('.seconds');

    var targetDateTime = new Date(targetDate + ' ' + targetTime).getTime();

    function updateCountdown() {
        var now = new Date().getTime();
        var distance = targetDateTime - now;

        var days = Math.floor(distance / (1000 * 60 * 60 * 24));
        var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        var seconds = Math.floor((distance % (1000 * 60)) / 1000);

        if (distance < 0) {
            clearInterval(interval);
            $countdown.text('Aktion abgelaufen!');
        } else {
            if (days > 0) {
                $days.text(days + ' Tage : ').show();
            } else {
                $days.hide();
            }
            $hours.text(hours + 'h : ');
            $minutes.text(minutes + 'm : ');
            $seconds.text(seconds + 's');
        }
    }

    updateCountdown();
    $countdown.addClass('show');

    var interval = setInterval(updateCountdown, 1000);
}

$(document).ready(function () {
    startCountdown('2025-02-28', '23:59:59'); // Define target date and time
});