

/**
 * Initializes all elements that have the attribute [data-bs-tooltip="true"]
 * with Bootstrap tooltips.
 */

import { Tooltip } from 'bootstrap';

export function initTooltips() {
    const tooltipTriggerList = document.querySelectorAll('[data-bs-tooltip="true"]');
    const tooltipList = [...tooltipTriggerList].map(el => new Tooltip(el));

    // If you need the tooltip object somewhere else, you could return it:
    // return tooltipList;
}

initTooltips(); // Initialization of the tooltips