/**
 * SimpleBar
 */

import SimpleBar from 'simplebar';

document.addEventListener("DOMContentLoaded", function () {
    const simplebarContainers = document.querySelectorAll('.simplebar');
    const initializedContainers = [];

    simplebarContainers.forEach(container => {
        const autoHide = container.classList.contains('woocommerce') ? false : true;
        new SimpleBar(container, {
            autoHide: autoHide,
            tabIndex: -1,
        });
        initializedContainers.push(container);
    });

    // if (initializedContainers.length > 0) {
    //     console.log('Folgende SimpleBar-Container wurden initialisiert:', initializedContainers);
    // } else {
    //     console.log('Keine SimpleBar-Container gefunden.');
    // }
});

