import {
  dependenciesValuesXcontainer,
  dependenciesValuesXvalues,
} from './../dependencies.js';

import {
} from '../variables.js';

document.addEventListener('DOMContentLoaded', () => {
  //*********************************/
  // Dependencies: Values X Values
  //*********************************/
  dependenciesValuesXvalues(
    "schminkspiegel_lichtfarbe",
    value => {
      return (
        value.includes("weiss")
      );
    },
    "schminkspiegel_bedienung",
    value => {
      return (
        value === "extra_touch_sensor_plus"
      );
    },
  );
  dependenciesValuesXvalues(
    "schminkspiegel_lichtfarbe",
    value => {
      return (
        value === "alle_drei_lichtfarben"
      );
    },
    "schminkspiegel_bedienung",
    value => {
      return (
        value !== "extra_touch_sensor_plus"
      );
    },
  );
  //*********************************/
  // Dependencies: Values X Container
  //*********************************/
  dependenciesValuesXcontainer(
    'schminkspiegel', value => {
      return (
        value !== ""
      );
    },
    'schminkspiegel_position'
  );
  dependenciesValuesXcontainer(
    'schminkspiegel', value => {
      return (
        value === "beleuchtet"
      );
    },
    'schminkspiegel_lichtfarbe'
  );
  dependenciesValuesXcontainer(
    'schminkspiegel', value => {
      return (
        value === "beleuchtet"
      );
    },
    'schminkspiegel_bedienung'
  );
  dependenciesValuesXcontainer(
    'schminkspiegel_bedienung', value => {
      return (
        value !== "" &&
        value !== "ohne_extra_schalter"
      );
    },
    'schminkspiegel_bedienung_position'
  );
});
