/**
 * Creates a Bootstrap modal dialog with custom options.
 * 
 * @param {Object}    options                     - Configuration options for the modal.
 * @param {string}   [options.id]                 - Unique ID for the modal (default: "myModal-" + timestamp).
 * @param {string}   [options.title]              - Modal title (default: "Notice").
 * @param {string}   [options.body]               - HTML content for the modal body (default: "<p>Some text goes here</p>").
 * @param {string}   [options.footer]             - Custom HTML content for the modal footer (e.g., your own buttons).
 * @param {string}   [options.dismiss]            - Label for the default close button (only used if no custom footer is provided; default: "Schlieen").
 * @param {string|boolean} [options.backdrop]     - Bootstrap backdrop option (true, false, or "static"; default: true).
 * @param {boolean}  [options.keyboard]           - Determines if the modal can be closed by pressing the ESC key (default: true).
 * @param {string}   [options.size]               - Optional: "sm", "lg", "xl" for modal size.
 * @param {boolean}  [options.centered]           - Whether to vertically center the modal (default: true).
 * @param {boolean}  [options.scrollable]         - Whether the modal body is scrollable (default: true).
 *
 * Lifecycle Hooks (Bootstrap events):
 * @param {function} [options.beforeShow]         - (optional async) Called before the modal is displayed.
 * @param {function} [options.onOpen]             - Triggered right before the modal opens (Event: show.bs.modal).
 * @param {function} [options.onShown]            - Triggered when the modal becomes visible (Event: shown.bs.modal).
 * @param {function} [options.onHide]             - Triggered right before the modal closes (Event: hide.bs.modal).
 * @param {function} [options.onClose]            - Triggered after the modal has closed (Event: hidden.bs.modal).
 *
 * Extended options for additional CSS classes & ARIA attributes:
 * @param {Object}   [options.extraClasses]
 * @param {string}   [options.extraClasses.modal]        - Classes for `<div class="modal fade">`.
 * @param {string}   [options.extraClasses.modalDialog]  - Classes for `<div class="modal-dialog">`.
 * @param {string}   [options.extraClasses.modalContent] - Classes for `<div class="modal-content">`.
 * @param {string}   [options.extraClasses.modalHeader]  - Classes for `<div class="modal-header">`.
 * @param {string}   [options.extraClasses.modalBody]    - Classes for `<div class="modal-body">`.
 * @param {string}   [options.extraClasses.modalFooter]  - Classes for `<div class="modal-footer">`.
 *
 * @param {Object}   [options.aria]
 * @param {string}   [options.aria.labelledby]           - Value for `aria-labelledby`.
 * @param {string}   [options.aria.describedby]          - Value for `aria-describedby`.
 *
 * Additional options for confirm/cancel buttons (only if NO custom footer is provided):
 * @param {boolean}  [options.showConfirm]       - Whether to create a "confirm" button.
 * @param {string}   [options.confirmText]       - Label for the confirm button (default: "OK").
 * @param {function} [options.onConfirm]         - Callback if the confirm button is clicked.
 * @param {boolean}  [options.showCancel]        - Whether to create a "cancel" button.
 * @param {string}   [options.cancelText]        - Label for the cancel button (default: "Abbrechen").
 * @param {function} [options.onCancel]          - Callback if the cancel button is clicked.
 *
 * @returns {Modal|null} The Bootstrap Modal instance, or `null` if `beforeShow` fails.
 */

import { Modal } from 'bootstrap';

export async function createModal(options = {}) {
  // 1) Default settings
  const defaults = {
    id: 'myModal-' + Date.now(),
    title: 'Notice',
    body: '<p>Some text goes here</p>',
    footer: '',
    dismiss: 'Close',
    backdrop: true,
    keyboard: true,
    size: '',
    centered: true,
    scrollable: true,

    // Lifecycle hooks
    beforeShow: null,
    onOpen: null,
    onShown: null,
    onHide: null,
    onClose: null,

    // CSS classes
    extraClasses: {
      modal: '',
      modalDialog: '',
      modalContent: '',
      modalHeader: '',
      modalBody: '',
      modalFooter: '',
    },

    // ARIA attributes
    aria: {
      labelledby: '',
      describedby: ''
    },

    // Confirm/Cancel handling
    showConfirm: false,
    confirmText: 'OK',
    onConfirm: null,

    showCancel: false,
    cancelText: 'Cancel',
    onCancel: null
  };

  // 2) Shallow merge user options with defaults
  const config = Object.assign({}, defaults, options);
  config.extraClasses = Object.assign({}, defaults.extraClasses, options.extraClasses);
  config.aria = Object.assign({}, defaults.aria, options.aria);

  // 3) Check if an element with the given ID already exists
  if (document.getElementById(config.id)) {
    console.warn(`A modal with the ID "${config.id}" already exists. Generating a new ID...`);
    config.id = 'myModal-' + performance.now();
  }

  // 4) Gather classes for .modal-dialog
  const dialogClasses = [];
  if (config.size) {
    dialogClasses.push(`modal-${config.size}`); // e.g., "modal-lg", "modal-sm"
  }
  if (config.centered) {
    dialogClasses.push('modal-dialog-centered');
  }
  if (config.scrollable) {
    dialogClasses.push('modal-dialog-scrollable');
  }

  // 5) If the user did NOT provide a custom footer, build it depending on showConfirm/showCancel
  let finalFooter = config.footer;
  if (!finalFooter) {
    let footerButtons = '';

    // Cancel button (if requested)
    if (config.showCancel) {
      footerButtons += `
        <button type="button" class="btn btn-dark" data-bs-dismiss="modal" data-role="cancel-btn">
          ${config.cancelText}
        </button>
      `;
    }

    // Confirm button (if requested)
    if (config.showConfirm) {
      footerButtons += `
        <button type="button" class="btn btn-primary" data-bs-dismiss="modal" data-role="confirm-btn">
          ${config.confirmText}
        </button>
      `;
    }

    // If neither confirm nor cancel is requested, provide a standard "Close" button
    if (!config.showConfirm && !config.showCancel) {
      footerButtons = `
        <button type="button" class="btn btn-dark" data-bs-dismiss="modal">
          ${config.dismiss}
        </button>
      `;
    }

    finalFooter = footerButtons;
  }

  // 6) Construct the modal HTML
  const modalHTML = `
    <div class="modal fade ${config.extraClasses.modal}"
         id="${config.id}"
         tabindex="-1"
         role="dialog"
         aria-modal="true"
         ${config.aria.labelledby ? `aria-labelledby="${config.aria.labelledby}"` : ''}
         ${config.aria.describedby ? `aria-describedby="${config.aria.describedby}"` : ''}>
      <div class="modal-dialog ${dialogClasses.join(' ')} ${config.extraClasses.modalDialog}">
        <div class="modal-content ${config.extraClasses.modalContent}">
          <div class="modal-header ${config.extraClasses.modalHeader}">
            <h5 class="modal-title"
                ${config.aria.labelledby ? `id="${config.aria.labelledby}"` : ''}>
                ${config.title}
            </h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div class="modal-body ${config.extraClasses.modalBody}">
            ${config.body}
          </div>
          <div class="modal-footer ${config.extraClasses.modalFooter}">
            ${finalFooter}
          </div>
        </div>
      </div>
    </div>
  `;

  // 7) Insert modal into the DOM
  document.body.insertAdjacentHTML('beforeend', modalHTML);
  const modalEl = document.getElementById(config.id);

  // 8) (Optional async) beforeShow callback
  if (typeof config.beforeShow === 'function') {
    try {
      const maybePromise = config.beforeShow();
      if (maybePromise instanceof Promise) {
        await maybePromise; // Wait for the promise to resolve
      }
    } catch (err) {
      console.error('Error in beforeShow:', err);
      // Clean up and abort
      modalEl.remove();
      return null;
    }
  }

  // 9) Create Bootstrap Modal instance
  const bsModal = new Modal(modalEl, {
    backdrop: config.backdrop,
    keyboard: config.keyboard
  });

  // 10) Set up event listeners (lifecycle hooks)
  modalEl.addEventListener('show.bs.modal', (evt) => {
    if (typeof config.onOpen === 'function') {
      config.onOpen(evt);
    }
  });

  modalEl.addEventListener('shown.bs.modal', (evt) => {
    if (typeof config.onShown === 'function') {
      config.onShown(evt);
    }
    // **Fix: Remove aria-hidden once the modal is fully shown**
    // modalEl.removeAttribute('aria-hidden');

    // **Optional Improvement: Set focus to the first focusable element within the modal**
    const focusableElements = modalEl.querySelectorAll('a[href], button, textarea, input, select, [tabindex]:not([tabindex="-1"])');
    if (focusableElements.length) {
      focusableElements[0].focus();
    }
  });

  modalEl.addEventListener('hide.bs.modal', (evt) => {
    if (typeof config.onHide === 'function') {
      config.onHide(evt);
    }
  });

  modalEl.addEventListener('hidden.bs.modal', (evt) => {
    if (typeof config.onClose === 'function') {
      config.onClose(evt);
    }
    modalEl.remove();
  }, { once: true });

  // 11) If confirm/cancel buttons were generated, register their click handlers
  if (!config.footer) {
    // Cancel
    if (config.showCancel && typeof config.onCancel === 'function') {
      const cancelBtn = modalEl.querySelector('[data-role="cancel-btn"]');
      if (cancelBtn) {
        cancelBtn.addEventListener('click', () => {
          config.onCancel();
        });
      }
    }

    // Confirm
    if (config.showConfirm && typeof config.onConfirm === 'function') {
      const confirmBtn = modalEl.querySelector('[data-role="confirm-btn"]');
      if (confirmBtn) {
        confirmBtn.addEventListener('click', () => {
          config.onConfirm();
        });
      }
    }
  }

  // 12) Show the modal
  bsModal.show();

  // 13) Return the Bootstrap Modal instance
  return bsModal;
}
