import {
  configuratorSummary,
  optionGroups,
} from './variables';

import {
  toNumber,
  formatPrice,
  getProductTitle,
  getProductPrice,
} from './functions';

/* ------------------------------------------------------------------
 * Build the summary HTML
 * ------------------------------------------------------------------ */
export function buildSummary({ productTitle, productPrice, optionsSummary }) {
  // Start output with card container
  let htmlOutput = `
        <h5 class="lh-sm bg-secondary-subtle border-secondary-subtle border-bottom p-3 mb-0">Zusammenfassung</h5>
        <ul class="list-group list-group-flush">
  `;

  // Base product line (Title + Price)
  htmlOutput += `
      <li class="list-group-item border-secondary-subtle">
        <span class="row g-1">
          <span class="col text-truncate fw-medium">${productTitle}</span>
          <span class="col-auto">${formatPrice(productPrice)}</span>
        </span>
      </li>
  `;

  // Option items or fallback if none
  if (optionsSummary.length === 0) {
    htmlOutput += `
      <li class="list-group-item text-danger">Keine Optionen ausgewhlt</li>
    `;
  } else {
    optionsSummary.forEach((item, index) => {
      const label = item.label;
      const value = item.radioLabel || item.inputValue || item.pricematrixLabel || '';
      const price = item.radioPrice || item.inputPrice || item.pricematrixPrice || 0;
      const formattedPrice = formatPrice(price) || '-';

      // Falls die Option aus einem "Child"-Element stammt, wird der HTML-Output angepasst:
      if (item.isChild) {
        htmlOutput += `
          <li class="list-group-item border-secondary-subtle text-muted small" style="margin-top: -8px;">
            <span class="row g-1">
              <span class="col-auto fw-medium">${label}:</span>
              <span class="col text-truncate">${value}</span>
              <span class="col-auto">${formattedPrice}</span>
            </span>
          </li>
        `;
      } else {
        htmlOutput += `
          <li class="list-group-item border-secondary-subtle">
            <span class="row g-1">
              <span class="col-auto fw-medium">${label}:</span>
              <span class="col text-truncate">${value}</span>
              <span class="col-auto">${formattedPrice}</span>
            </span>
          </li>
        `;
      }
    });
  }

  // Calculate extra price and total price
  const extraPrice = optionsSummary.reduce(
    (acc, item) =>
      acc + toNumber(item.inputPrice) + toNumber(item.radioPrice) + toNumber(item.pricematrixPrice),
    0
  );

  const totalPrice = productPrice + extraPrice;
  const totalPriceFormatted = formatPrice(totalPrice.toFixed(2));

  // Close tags and add total price
  htmlOutput += `
        </ul>
        <p class="bg-secondary-subtle border-top border-secondary-subtle text-end lh-sm px-3 py-2 mb-0 fs-5">
          <span class="product-price-total-text small">Gesamtpreis:</span>
          <span class="product-price price fw-medium">${totalPriceFormatted || '0,00 '}</span>
        </p>
  `;

  return htmlOutput;
}

/* ------------------------------------------------------------------
 * Main function to update the summary
 *   1) Determine base price & product title
 *   2) Gather relevant data (options)
 *   3) Build HTML output
 *   4) Insert summary into the container
 * ------------------------------------------------------------------ */
export function updateSummary() {
  // Determine base price & product title
  const productPrice = getProductPrice();
  const productTitle = getProductTitle();

  // Gather relevant data (options)
  const optionsSummary = [];
  optionGroups.forEach((group, index) => {
    const label = group.dataset.label || 'Unbekannte Option';

    // Prfen, ob die Option-Gruppe das Kind-Flag besitzt
    const isChild = group.classList.contains('option-group-child');

    // Retrieve pricematrix (select) fields
    const pricematrixEl = group.querySelector('.option-price[class*="pxbh"]');
    let pricematrixLabel = null;
    let pricematrixPrice = null;

    if (pricematrixEl && pricematrixEl.value.trim() !== '') {
      const selectedOption = pricematrixEl.selectedOptions[0];
      pricematrixLabel = selectedOption?.dataset?.label?.trim() || '';
      pricematrixPrice = selectedOption?.dataset?.price || null;
    }

    // Retrieve input fields
    const inputEl = group.querySelector('.option-input');
    let inputValue = null;
    let inputPrice = null;

    if (inputEl && inputEl.value.trim() !== '') {
      inputValue = inputEl.value.trim();
      inputPrice = inputEl.dataset.price || null;
    }

    // Retrieve selected radio field (if any)
    const radioEl = group.querySelector('.option-radio:checked');
    let radioLabel = null;
    let radioPrice = null;

    if (radioEl && radioEl.value.trim() !== '') {
      radioLabel = radioEl.dataset.label || radioEl.value.trim();
      radioPrice = radioEl.dataset.price || null;
    }

    // Falls mindestens ein Wert gesetzt wurde, Option hinzufgen
    if (inputValue || radioLabel || pricematrixLabel) {
      const optionItem = {
        label,
        pricematrixPrice,
        inputValue,
        inputPrice,
        radioLabel,
        radioPrice,
        isChild, // Kennzeichnet, dass es sich um eine "Child"-Option handelt
      };
      optionsSummary.push(optionItem);
    }
  });

  // Build HTML output
  const summaryHtml = buildSummary({
    productTitle,
    productPrice,
    optionsSummary,
  });

  // Insert summary into the target container
  if (configuratorSummary) {
    configuratorSummary.innerHTML = summaryHtml;
  }
}
